<template>
    <div class="row">
      <div class="col-md-4"
       v-for="user in userlist" :key="user.id">    
            <StatisticCardHorizontal
            :statistic="user.line_name" :statistic-title="user.line_name" style="max-width:500px"/>
            <img :src="user.line_avatar" height="300" width="300">
      </div>
    </div>
</template>

<script>
import axios from 'axios'
import StatisticCardHorizontal from '@core/components/statistics-cards/StatisticCardHorizontal.vue'
import { BCard, BCardText, BLink } from 'bootstrap-vue'
export default {
    components: {
    BCard,
    BCardText,
    BLink,
    StatisticCardHorizontal,
  },
    data() {
        return{
            userlist: []
        }
    },
    methods: {
        event(){
        axios.get('http://127.0.0.1:8000/api/user')
        .then(response => (this.userlist = response.data))
        }
    },
    mounted () {
    this.event()
    this.timer = setInterval(this.event, 10000)
  }
}
</script>